.container{
    text-align: center;
}


* {
    margin: 0;
    padding: 0;
}

body {
    background: #71668f;
}

h1 {
    text-align: center;
    font-family: 'Poppins', sans-serif;
    color: #fff;
    font-size: 20px;
}

.painel {
    margin-top: 30px;
}

.formulario {
    display: flex;
    justify-content: center;
}

.formulario label {
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
    font-weight: 400;
    display: flex;
    justify-content: center;
    font-size: 30px;
    color: #fff;
}

#cep {
    width: 500px;
    padding: 10px;
    background: #fff2;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    border: 2px solid #5429cf;
    color: #fff;
    font-size: 13px;
    font-family: 'Poppins', sans-serif;
}

#btnCep {
    border-left: none;
    padding: 14px 26px 15px;
    margin-left: -8px;
    background-color: #5429cf;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    cursor: pointer;
    color: #fff;
    text-transform: uppercase;
    border: none
}

#btnCep:onfocus {
    border: none;
}

.resultadosDados {
    display: flex;
    width: 100%;
    max-width: 1040px;
    justify-content: center;
    align-items: center;
    max-width: 1080px;
    margin: 10px auto;
    flex-wrap: wrap;
}

.box-info {
    margin-top: 10px;
    margin-right: 30px;
}

.info {
    border: 2px solid #5429cf;
    padding: 10px 30px;
    margin: 15px 0;
    border-radius: 2px;
    color: #fff;
    font-family: 'Poppins', sans-serif;
    width: 150px;
}

.modal-container {
    height: 100vh;
    width: 100%;
    background: rgba(0, 0, 0, .5);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2000;
    display: none;
    justify-content: center;
    align-items: center;
}

.modal-container.ativo {
    display: flex;
}

.modal {
    background-color: white;
    padding: 40px;
    box-shadow: 0 0 0 10px white;
    position: relative;
    min-width: 300px;
    border-radius: 2px
}

.modal h1 {
    color: #000
}

@keyframes modal {
    from {
        opacity: 0;
        transform: translate3d(0, -30px, 0);
    }
    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}

.ativo .modal {
    animation: modal .3s forwards;
}

.fechar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 4px solid white;
    top: -30px;
    right: -30px;
    position: absolute;
    background-color: #5429cf;
    box-shadow: 0 4px 4px 0 rgba(0, 0, .3);
    font-size: 1.2rem;
    color: white;
    font-family: 'Poppins', sans-serif;
    cursor: pointer;
}

.modal label {
    font-family: 'Poppins', sans-serif;
    display: block;
    font-weight: bold;
    margin: 1rem 0 .5rem 0;
}

.modal input {
    width: 100%;
    padding: .7rem .5rem;
    box-sizing: border-box;
}

.modal button[type="submit"] {
    display: block;
    background-color: #e54;
    border: none;
    border-radius: 4px;
    padding: .7rem 2rem;
    font-size: 1rem;
    color: white;
    font-family: 'Poppins', sans-serif;
    margin-top: 1rem;
    font-weight: bold;
    cursor: pointer;
}

.box_img {
    display: flex;
    justify-content: center;
}

img {
    width: 56%;
    animation: show-right .5s forwards;
}

@keyframes show-right {
    from {
        opacity: 0;
        transform: translate3d(-30px, 0, 0);
    }
    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
        ;
    }
}

@media (max-width: 700px) {
    #cep {
        width: 60%;
    }
    #btnCep {
        width: 30%;
        font-size: 13px;
        padding: 14px 18px 15px;
    }
    .modal {
        min-width: 0;
    }
    img {
        width: 100%;
    }
    .box-info {
        margin-right: 0;
    }
}

